.news-banner2{
  &__wrap{
    padding: 30px 0 90px;
    border-bottom: 1px solid rgba(#707070,.18);
  }
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 478px) 480px;
  }
  &__col{
    position: relative;
    z-index: 2;
  }
  &__col2{
    margin-left: -2px;
    border-radius: 0 10px 10px 0;
    border: 1px solid #AFAFAF;
    padding: 60px 53px 40px 60px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__img{
    height: 360px;
    min-height: 100%;
    border-radius: 10px 0 0 10px;
    img{
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
      border-radius: inherit;
    }
    ._view2{
      display: none;
    }
  }
  &__info{
    font-family: $font1;
    font-weight: 600;
    margin-bottom: 48px;
  }
  &__title{
    font-size: 28px;
    line-height: 34px;
  }
  &__desc{
    margin-top: 24px;
    font-size: 20px;
    line-height: 120%;
  }
  &__link{
    margin-top: auto;
  }
}


@media screen and (max-width: 992px){
  .news-banner2{
    &__wrap{
      padding: 30px 0;
      border-bottom: none;
    }
    &__inner{
      grid-template-columns: 100%;
    }
    &__col2{
      margin: -2px 0 0;
      border-radius: 0 0 10px 10px;
      padding: 30px 24px 34px;
    }
    &__img{
      border-radius: 10px 10px 0 0;
      height: auto;
      img{
        height: auto;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .news-banner2{
    &__img{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__title{
      font-size: 20px;
      line-height: 140%;
    }
    &__desc{
      font-size: 16px;
      line-height: 19px;
      margin-top: 18px;
    }
  }
}
