.m-news{
  &__top{
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
  }
  &__tabs{
    display: flex;
  }
  &__tab{
    @include center;
    padding: 0 18px;
    height: 34px;
    font-size: 18px;
    color: #444444;
    border-radius: 30px;
    cursor: pointer;
    transition: all .3s;
    &:hover{
      color: $primary;
    }
    &--active{
      background-color: #F8F8F8;
      color: $primary;
      pointer-events: none;
    }
  }
}

.news-main2{
  padding: 60px 0;
}

.news-grid{
  display: grid;
  grid-template-columns: repeat(3,calc(100% / 3 - 68px / 3));
  gap: 60px 34px;
  margin-top: 30px;
}

.news-bottom{
  padding-bottom: 60px;
  .pagination{
    margin-top: 0;
    &__more{
      margin-top: 0;
    }
  }
}

.news-banners{
  margin-top: 42px;
  .slider__wrap{
    border-radius: 10px;
  }
}

.news-banner{
  height: 540px;
  position: relative;
  color: $white;
  &__img{
    height: 100%;
    border-radius: 10px;
    position: relative;
    img{
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
      border-radius: inherit;
    }
    ._view2{
      display: none;
    }
  }
  &__bottom{
    background-image: url(../img/banner-bg.svg);
    background-size: cover;
    background-position: center;
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    padding: 40px 46px 53px;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
  }
  &__category,
  &__date{
    font-size: 20px;
    line-height: 120%;
  }
  &__title{
    margin-top: 22px;
    font-size: 32px;
    line-height: 125%;
    font-weight: 600;
    font-family: $font1;
  }
  &__link{
    color: $white;
  }
  &__top{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    padding: 44px 46px;
  }
}


@media screen and (max-width: 992px){
  .news-grid{
    grid-template-columns: repeat(2,calc(50% - 10px));
    gap: 40px 20px;
    margin-top: 22px;
  }

  .news-main2{
    padding: 30px 0;
  }

  .m-news{
    &__top{
      display: block;
    }
    &__tabs{
      margin-top: 24px;
    }
    &__tab{
      height: 32px;
      font-size: 14px;
    }
  }

  .news-banners{
    margin-top: 15px;
  }

  .news-banner{
    height: 470px;
    &__bottom{
      display: block;
      padding: 18px 22px 30px;
      &-col2{
        margin-top: 34px;
      }
    }
    &__title{
      margin-top: 17px;
      font-size: 20px;
      line-height: 140%;
      max-width: 240px;
    }
    &__category,
    &__date{
      font-size: 16px;
    }
    &__top{
      padding: 26px 22px;
    }
  }
}


@media screen and (max-width: 576px){
  .news-grid{
    grid-template-columns: 100%;
  }

  .news-banner{
    &__img{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }
}
