@import "mixins";
@import "vars";
@import "components/fonts";
@import "pages/a-index";
@import "pages/b-news";
@import "pages/c-news";
@import "pages/d-news-item";
.cookie-modal{
  position: fixed;
  z-index: 21;
  left: 16px;
  bottom: 16px;
  background-color: #fff;
  box-shadow: 0 0 8px 4px rgba(0,0,0, .15);
  width: 364px;
  max-width: calc(100vw - 32px);
  padding: 16px;
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  &._active{
    opacity: 1;
    pointer-events: unset;
  }
  &__nav{
    margin-top: 10px;
    display: flex;
  }
  &__text{
    font-size: 12px;
    line-height: 130%;
    a{
      color: $primary;
    }
  }
  &__btn{
    padding: 10px 30px;
    font-weight: 400;
    font-size: 14px;
  }
}
