.news-slider2{
  .swiper{
    &-container{
      width: calc(100% + 7px);
    }
    &-slide{
      width: 25%;
      padding-right: 7px;
    }
  }
}

.detailed-news-container{
  padding-bottom: 0;
  padding-top: 0;
}

.detailed-news{
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 420px) 360px;
    gap: 60px;
    margin-top: 40px;
    padding-bottom: 59px;
    border-bottom: 1px solid rgba(112, 112, 112, 0.18);
  }
  &__desc{
    max-width: 800px;
    margin-top: 30px;
    margin-bottom: 5px;
  }
  &__img{
    @include imgCover(65%);
    border-radius: 10px;
  }
  &__text{
    color: #333;
    h2{
      font-size: 24px;
      line-height: 120%;
      font-weight: 700;
      margin-bottom: 25px;
    }
    h3{
      font-size: 20px;
      line-height: 130%;
      font-weight: 700;
      margin-bottom: 25px;
    }
    ol,ul{
      @include grid100gap(16px);
      margin-bottom: 26px;
      &:last-child{
        margin-bottom: 0;
      }
    }
    li{
      font-size: 16px;
      line-height: calc(26 / 16);
      color: var(--gray-600);
      font-weight: 400;
      position: relative;
      padding-left: 20px;
      &:before{
        position: absolute;
        top: 0;
        left: 0;
      }
    }
    ul{
      li{
        &:before{
          content: 'â€¢';
          font-size: 24px;
          color: $primary;
          top: -7px;
        }
      }
    }
    ol {
      counter-reset: list;
      li {
        counter-increment: list;
        &:before {
          content: counter(list) '. ';
        }
      }
    }
  }
  &__col2{
    @media screen and (min-width: 993px){
      .news-banner2{
        &__wrap{
          padding: 0;
          border-bottom: none;
        }
        &__inner{
          grid-template-columns: 100%;
        }
        &__col2{
          margin: -2px 0 0;
          border-radius: 0 0 10px 10px;
          padding: 30px 24px 34px;
          border-color: rgba(#707070,.18);
        }
        &__img{
          border-radius: 10px 10px 0 0;
          height: auto;
          img{
            height: auto;
          }
        }
        &__title{
          font-size: 20px;
          line-height: 140%;
        }
        &__desc{
          font-size: 16px;
          line-height: 24px;
          margin-top: 16px;
        }
      }
    }
  }
}

.news-main3{
  padding: 60px 0 70px;
  .news__title{
    font-size: 26px;
  }
  @media screen and (min-width: 993px){
    .swiper-pagination{
      display: none;
    }
  }

}

.article-slider{
  margin-bottom: 50px;
  .slider__wrap{
    overflow: hidden;
  }
  .detailed-news__img{
    margin-bottom: 0;
  }
}

.detailed-news-page{
  .news-item__date{
    margin: 20px 0 -20px;
  }
}


@media screen and (max-width: 992px){
  .detailed-news{
    &__grid{
      grid-template-columns: 100%;
      gap: 0;
      padding-bottom: 0;
      margin-top: 30px;
      border-bottom: none;
    }
    &__col2{
      margin-top: 30px;
      padding-bottom: 15px;
      border-top: 1px solid rgba(112, 112, 112, 0.18);
      @media screen and (min-width: 577px){
        .news-banner2{
          &__img{
            @include imgCover(62%);
          }
        }
      }
      .news-banner2{
        &__wrap{
          padding: 60px 0 0;
        }
      }
    }
  }

  .news-main3{
    padding: 45px 0 49px;
    .news__title{
      font-size: 20px;
    }
  }
}


@media screen and (max-width: 768px){
  .detailed-news-page{
    overflow: hidden;
  }

  .detailed-news{
    &__text{
      h2,h3{
        font-size: 20px;
        margin-bottom: 20px;
      }
      //h3{
      //  font-size: 16px;
      //  margin-bottom: 20px;
      //}
    }
  }

  .news-slider2{
    .slider__wrap{
      overflow: unset;
    }
    .swiper{
      &-container{
        width: calc(100% + 5px);
      }
      &-slide{
        width: 162px;
        padding-right: 5px;
      }
      &-pagination-bullet{
        width: 10px;
      }
    }
  }

  .article-slider{
    margin-bottom: 40px;
  }
}


