.news-main{
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    //.presentation__nav{
    //  margin-top: 0;
    //}
  }
  .swiper-nav{
    position: absolute;
    bottom: 100%;
    right: 0;
    margin-bottom: 35px;
    display: flex;
  }
  .swiper-button{
    position: static;
    margin: 0 0 0 26px;
    width: 46px;
    svg{
      width: 100%;
      height: auto;
    }
    &:hover{
      path{
        stroke: $primary;
      }
    }
  }
}

.article-slider,
.news-banners,
.news-slider{
  .slider__wrap{
    overflow: hidden;
  }
  .swiper {
    &-container {
      width: calc(100% + 34px);
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
      padding-right: 34px;
    }
    &-pagination{
      position: static;
      margin-top: 15px;
      @include center;
      &-bullet{
        width: 14px;
        height: 5px;
        border-radius: 40px;
        opacity: 1;
        margin: 0 3.5px !important;
        border: 1px solid #E0E0E0;
        background-color: transparent;
        &-active{
          border-color: $primary;
        }
      }
    }
  }
}

.news-slider{
  margin-top: 30px;
  .swiper{
    &-slide{
      width: calc(100% / 3);
    }
  }
}

.m-news-card{
  border-radius: .625em;
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #F8F8F8;
  &__img{
    @include imgCover(65.1%);
    border-radius: .625em .625em 0 0;
  }
  &__content{
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    padding: 22px 17px 28px;
  }
  &__category{
    font-size: 16px;
    line-height: 125%;
    color: #888888;
    font-weight: 500;
    font-family: $font1;
  }
  &__title{
    font-size: 17px;
    line-height: 141%;
    font-weight: 600;
    margin-top: 21px;
    font-family: $font1;
  }
  &__text{
    margin-top: 16px;
    min-height: 36px;
  }
  &__desc{
    font-size: 14px;
    line-height: 143%;
    color: #707070;
    @include truncate(1);
  }
  &__date{
    margin-top: auto;
    padding-top: 22px;
    font-size: 16px;
    line-height: 150%;
    color: $primary;
  }
}


@media screen and (max-width: 992px){
  .news-slider{
    margin-top: 22px;
    .swiper{
      &-slide{
        width: 50%;
      }
    }
  }

  .news-main{
    .swiper-nav{
      display: none;
    }
    .presentation__nav{
      margin-top: 22px;
    }
  }
}


@media screen and (max-width: 576px){
  .news-slider{
    .swiper{
      &-slide{
        width: 100%;
      }
    }
  }

  .m-news-card{
    &__title{
      font-size: 16px;
      line-height: 150%;
    }
  }
}
